body {
  background-color: rgba(241, 241, 241, 0.399) !important;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

/* FORMS */

.rs-form:not(.rs-form-inline) .rs-form-group:not(:last-child) {
  margin-bottom: 0px
}

.rs-form-control-wrapper {
  width: 100%
}
.rs-form-control-wrapper input{
  width: 100% !important;
}

.registerForm {
  margin-top: 25%;
}

.loginForm {
  margin-top: 25%;
}

.hasHomeForm {
  margin-top: 25%;
  margin-bottom: 25%;
}

.paddingHome{
  margin-top: 20px
}

.rs-panel {
  background-color: white;
}

/* FORMS */

/* NAVBAR */

.rs-icon.fill-color use{
  fill: currentColor;
}

.navHomeIcon:hover {
  color: #FF7B89;
  transition: color 1s;
}

.navUserIcon:hover {
  color: rgb(163, 68, 151);
  transition: color 1s;
}

.navListIcon {
  font-size: 25px !important;
}

.navListIcon:hover {
  color: #6F5F90;
  transition: color 1s;
}

.navGearIcon {
  font-size: 25px !important;
}
.navGearIcon:hover {
  color: rgb(85, 208, 66);
  transition: color 1s;
}

.navListIcon:hover {
  color: #758EB7;
  transition: color 1s;
}

.navBar {
  z-index: 9999;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: white;
  box-shadow: 0px 0px 88px -21px rgba(0,0,0,0.20);
}

/* NAVBAR */


/* MAIN */
.main {
padding-bottom: 90px
}

.main h1 {
  color: white;
  padding-top: 10px;
  padding-left: 30px;
  font-size: 3em;
  font-weight: 900;
  text-shadow: 0px 0px 17px rgba(0,0,0,0.38);
}

.homeImg {
  width: 100%;
  padding: 0px;
  top: 0px;
  position: absolute;
  object-fit: cover;
  z-index: -1;
  height: auto ;
  max-height: 208px !important;
}

.infoPanel {
  margin-top: 100px;
  margin-left: 30px;
  margin-right: 30px;
}

.main h3 {
  font-size: 1.2em;
  font-weight: 400
}

.scrolling-wrapper {
  display: -webkit-flex;
  display: flex;
  overflow-x: auto;
  margin-top: 20px;
}

::-webkit-scrollbar { 
  display: none; 
}

.usersDiv {
  position: absolute;
  right: 28px;
}

.usersDiv img {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin: 2px;
box-shadow: 0px 0px 2px 2px rgba(0,0,0,0.25);
}

.box {
  width: 60px;
  height: 60px;
  margin: 10px;
  border-radius: 40px;
}

.clean {
  background-image: linear-gradient(45deg, #84fab0 0%, #8fd3f4 100%);
}

.dirty {
  background-image: linear-gradient(-45deg, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
}

.medium {
  background-image: linear-gradient(-45deg, #ffe6d2 0%, #fcb69f 100%);
}

.box i{
  position: relative;
  color: white;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.roomDiv p {
  text-align: center;
  margin-top: -3px;
}

.shoppingList {
  margin-top:30px;
  margin-left: 30px;
  margin-right: 30px;
}

.datesList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.date {
  border-radius: 5px;
  margin:2px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: white;
  width: 80px;
  height: 80px;
  background-image: linear-gradient(45deg, #5ee7df 0%, #b490ca 100%);
}

.date h2 {
  font-weight: 900;
  margin: 0px;
}

