/* CONFIG */

.config {
  padding-bottom: 90px
  }

.mainh1 {
  padding-top: 10px;
  padding-left: 30px;
  font-size: 3em;
  font-weight: 900;
}

.rs-form-control-wrapper textarea {
  min-width: none;
  min-height: none;
