  max-height: none;
}
.itemMap {
  margin-top: 10px;
  margin-bottom: 20px;
}


/* SUMMARY */


.circleUser {
  position: absolute;
  left: -14px;
  top: -10px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  object-fit: cover;
}

.logItem h3 {
  margin-bottom: 3px;
}
.roomPopover button {
  margin: 5px
}
.roomPopover h3 {
  font-size: 1.5em;
  text-align: center;
  color: #4ac0c0
}
.item {
  position: relative;
  margin-bottom: 10px;
  width: 100%;
  height: 50px;
  pointer-events: auto;
  -webkit-transform-origin: 50% 50% 0px;
          transform-origin: 50% 50% 0px;
  padding-left: 32px;
  padding-right: 32px;
  box-sizing: border-box;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  border-radius: 5px;
  box-shadow: 0px 10px 10px -5px rgba(0, 0, 0, 0.2);
}

.fg {
  cursor: -webkit-grab;
  background-color: #272727;
  color: rgba(255, 255, 255, 0.8);
  position: absolute;
  height: 100%;
  width: 100%;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  text-align: left;
  padding-left: 32px;
  border-radius: 5px;
  box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.2);
  font-size: 14.5px;
  text-transform: capitalize;
  letter-spacing: 2px;
  transition: box-shadow 0.75s;
}

.fg:active {
  cursor: -webkit-grabbing;
  box-shadow: 0px 15px 30px -5px rgba(0, 0, 0, 0.4);
}

.fg > * {
  pointer-events: none;
}

.av {
  position: absolute;
  width: 60px;
  height: 60px;
  background: url(/Homing-Web/static/media/checked.eb361219.svg);
}

.avx {
  position: absolute;
  width: 60px;
  height: 60px;
  background: url(/Homing-Web/static/media/cancel.474aef9e.svg);
}
.draggable-main {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Raleway', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: #f0f0f0;
}

.content {
  position: relative;

}

.content > div {
  position: absolute;
  width: 100%;
  height: 50px;
  overflow: visible;
  pointer-events: auto;
  -webkit-transform-origin: 50% 50% 0px;
          transform-origin: 50% 50% 0px;
  border-radius: 5px;
  color: white;
  line-height: 50px;
  padding-left: 32px;
  font-size: 14.5px;
  background: lightblue;
  text-transform: capitalize;
  letter-spacing: 2px;
}

.content > div:nth-child(1) {
  background: linear-gradient(135deg, #f6d365 0%, #fda085 100%);
}
.content > div:nth-child(2) {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
}
.content > div:nth-child(3) {
  background: linear-gradient(135deg, #5ee7df 0%, #b490ca 100%);
}
.content > div:nth-child(4) {
  background: linear-gradient(135deg, #c3cfe2 0%, #c3cfe2 100%);
}

